","sourcesContent":["* {\n\t-webkit-box-sizing: border-box;\n\tbox-sizing: border-box;\n}\nhtml {\n\twidth: 100%;\n\theight: 100%;\n\ttext-rendering: optimizeLegibility !important;\n\t-webkit-font-smoothing: antialiased !important;\n}\nbody {\n\twidth: 100%;\n\theight: 100%;\n\tfont-family: $f-primary;\n\tbackground-color: #fff;\n\tcolor: #747474;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\tfont-family: $f-primary;\n\tfont-weight: normal !important;\n\tcolor: #000;\n\t-webkit-font-smoothing: antialiased;\n}\nh1 { font-size: 36px; line-height: 45px; }\nh2 { font-size: 30px; line-height: 40px; }\nh3 { font-size: 24px; line-height: 35px; }\nh4 { font-size: 18px; line-height: 30px; }\nh5 { font-size: 14px; line-height: 25px; }\nh6 { font-size: 12px; line-height: 20px; }\n\nh1 small { font-size: 50%; }\nh2 small { font-size: 60%; }\nh3 small { font-size: 70%; }\nh4 small { font-size: 80%; }\nh5 small { font-size: 90%; }\nh6 small { font-size: 90%; }\n\nsmall {\n\tfont-size: 90%;\n\topacity: .7;\n\tfont-family: $f-primary;\n}\n\ncode { text-transform:none; font-family: monospace, serif;}\n\np {\n\tfont-family: $f-primary !important;\n\tmargin: 0 0 25px 0;\n\tfont-size: 13px;\n\tline-height: 25px;\n\tcolor: #747474;\n}\np strong { font-weight: 600; }\n.lead {\n\tfont-size: 14px;\n\tfont-weight: 400;\n\tline-height: 25px;\n}\n.dropcap {\n\tfloat: left;\n\twidth: 0.7em;\n\tfont-size: 400%;\n\tfont-family: algerian, courier;\n\tline-height: 80%;\n\t-webkit-border-radius: 2px;\n\t-moz-border-radius: 2px;\n\tborder-radius: 2px;\n\tpadding-left: 3px;\n\tmargin-top: 5px;\n\tmargin-right: 5px;\n}\n\na {\n\tcolor: #111;\n\toutline: none;\n\t-webkit-transition: all .2s ease-in-out;\n\t-moz-transition: all .2s ease-in-out;\n\ttransition: all .2s ease-in-out;\n}\na:hover,\na:focus {\n\tcolor: #111;\n\ttext-decoration: none;\n\toutline: none;\n}\nsmall {\n\tdisplay: block;\n\tmargin-top: 5px;\n}\n::-moz-selection { /* Code for Firefox */\n\tcolor: #fff;\n}\n\n::selection {\n\tcolor: #fff;\n}\n\n\n","////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////\n//// COLORS\n\n$c-white        : #fff;\n$c-grey-1\t\t: #e0e0e0;\n$c-grey-2\t\t: #e1e1e1;\n$c-grey-3       : #cfcfcf;\n$c-grey-4       : #999999;\n$c-grey-5\t\t: #5f6467;\n$c-grey-6       : #4c4f4e;\n$c-grey-7       : #4d5150;\n$c-black        : #000;\n$c-yellow-1\t\t: #ffc501;\n$c-blue-1\t\t: #286090;\n$c-blue-2       : #3dace1;\n$c-blue-3\t\t: #5bc0de;\n$c-green-1      : #5cb85c;\n$c-green-2      : #b2cc71;\n$c-green-3      : #1abc9c;\n$c-red-1        : #ff4530;\n$c-red-2\t\t: #fd3635;\n$c-red-3\t\t: #c9302c;\n$c-pink-1\t\t: #fd40b3;\n$c-orange-1     : #ff8b34;\n$c-orange-2     : #ec971f;\n$c-purple-1\t\t: #a85ad4;\n\n////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////\n//// FONTS\n\n$f-primary\t\t\t: 'Open Sans', sans-serif;\n$f-secondary\t\t:'Pacifico', cursive;\n\n$static_path: '../static/';\n$img_path: '../static/img/';\n$fonts_path: '../static/fonts/';\n","/*  ----------------------------------------------------\n2. GENERAL STYLES\n-------------------------------------------------------- */\n.animated { visibility:hidden; }\n.visible { visibility:visible; }\n\n.font-pacifico    { font-family: $f-secondary !important; }\n.font-open-sans   { font-family: $f-primary !important; }\n.font-montserrat  { font-family: $f-primary !important; }\n.font-source-sans-pro  { font-family: $f-primary !important; }\n\n.fs-75 { font-size: 75px !important; }\n.fs-100 { font-size: 100px !important; }\n.font-size-light { font-weight: 200 !important; }\n.font-size-normal { font-weight: 300 !important; }\n.font-size-bold { font-weight: 400 !important; }\n\n.strong { font-weight: 600; }\n\n\n\n.intro-direction {\n\tposition: absolute;\n\tleft: 50%;\n\tbottom: 5%;\n}\n.mouse-icon {\n\tborder: 2px solid #fff;\n\theight: 40px;\n\twidth: 24px;\n\tdisplay: block;\n\tz-index: 10;\n\topacity: 0.7