var data = {
	slides: [
		{
			marginTop: 50,
			animationType: 'bounceInLeft',
			animationDelay: 250,
			textColor: 'light',
			textAlign: 'right'
		},
		{
			marginTop: 0,
			animationType: 'bounceInUp',
			animationDelay: 250,
			textColor: 'dark',
			textAlign: 'center'
		},
		{
			marginTop: 10,
			animationType: 'bounceInRight',
			animationDelay: 250,
			textColor: 'light',
			textAlign: 'right'
		}
	]
};

export default data;



